.goods__inner {
  padding-top: 30px;
}

.goods__list {
  .ul();
}

.goods__item {
  width: 260px;
  margin-bottom: 30px;
}

.goods__link-img {
  display: block;
  height: 195px;
}

.goods__image {
  width: 260px;
  height: 195px;
}

.goods__content {
  padding-left: 28px;

  border: 1px solid @border-gray;
}

.goods__title {
  margin: 0;
  padding-top: 27px;
  padding-bottom: 6px;

  font-size: inherit;
  line-height: inherit;
}

.goods__description {
  margin: 0;

  font-size: 13px;
  font-weight: normal;
  line-height: 30px;
  color: #444444;
}

.price-and-basket {
  display: flex;
  padding-top: 42px;
  padding-bottom: 25px;
}

.price-and-basket__price {
  margin: 0;
  padding-right: 111px;

  font-size: 15px;
  line-height: 24px;
}

.price-and-basket__basket {
  padding: 0;
}

@media (min-width: @tablet-width) {
  .goods__inner {
    padding-top: 79px;
  }

  .goods__item {
    display: flex;
    width: 668px;
    margin-bottom: 54px;
  }

  .goods__image {
    width: 334px;
    height: 334px;
  }

  .goods__content {
    width: 335px;
  }

  .goods__title {
    font-size: 20px;
    line-height: 24px;
  }

  .goods__description {
    font-size: 15px;
  }

  .price-and-basket {
    padding-top: 196.5px;
  }

  .price-and-basket--toys {
    padding-top: 172px;
  }

  .price-and-basket__price {
    padding-right: 165px;

    font-size: 17px;
  }
}

@media (min-width: @desktop-width) {
  .goods__inner {
    padding-top: 128px;
    padding-bottom: 57px;
  }

  .goods__list {
    display: flex;
    flex-wrap: wrap;
  }

  .goods__item {
    display: block;
    width: 315px;
    margin-bottom: 0;
    margin-right: 52.5px;
  }

  .goods__item:last-child {
    margin-right: 0;
  }

  .goods__link-img {
    height: 462px;
  }

  .goods__image {
    width: 316px;
    height: 462px;
  }

  .goods__content {
    width: 315px;
  }

  .price-and-basket {
    padding-top: 61px;
  }

  .price-and-basket--toys {
    padding-top: 36px;
  }
}
